body {
    margin: 0;
    padding: 0;
    font-family: Helvetica , Arial, sans-serif;
    background-color: #efefef;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
.App{
  margin: 30px;
  padding: 30px;
  border-radius: 5px;
  background-color: #fff;
  box-shadow: 3px 3px 3px #aaa;
}

.formulario{
  margin: 15px 0 0 0;
}

.pesquisa {
  margin: 10px;
  width: 200px;
}

.pesquisa > input{
  padding: 8px 15px;
  font-size: 1rem;
  border: 1px solid #aaa;
  border-radius: 10px;
  width: 100%;
}

.Opcoes{
  display: flex;
  flex-direction: row;
  align-items: center;
}

.ordenacao{
  margin: 10px;
  padding-left: 30px;  
}

.ordenacao > label{
  font-size: 0.8rem;
  padding: 0 5px;
}

.ordenacao > select{
  font-size: 1.1rem;
  border-radius: 5px;
}

.botao{
  margin: 10px;
  padding: 8px 15px;
  /*border: 1px solid #272727;*/
  border-radius: 8px;
  color: #272727;
  background-color: #fefefe;
  font-size: 1rem;
  cursor: pointer;
}

.botao-verde{
  color: #fefefe;
  background-color: #26ad3d;
}
.botao-vermelho{
  color: #fefefe;
  background-color: #c91e1e;
}
.botao-vermelho-tabela{
  color: #fefefe;
  background-color: #c91e1e;
}
.botao-azul{
  color: #fefefe;
  background-color: #2a58b5;
}
.botao-azul-tabela{
  color: #fefefe;
  background-color: #2a58b5;
}

.botao-verde:hover{
  background-color: #2fd64c;
}
.botao-vermelho:hover{
  background-color: #f22424;
}
.botao-vermelho-tabela:hover{
  background-color: #f22424;
}
.botao-azul:hover{
  background-color: #3975ed;
}
.botao-azul-tabela:hover{
  background-color: #3975ed;
}

.input{
  margin: 10px 0;
}

.input > label {
  display: block;
  padding: 3px;
}

.input > input{
  margin: 5px;
  padding: 8px 15px;
  font-size: 1rem;
  border: 1px solid #777;
  border-radius: 8px;
}

.input > small {
  color: #ff0000
}

th, td {
  padding: 15px
}

.btn{
  border-radius: 25px;
}
  